import { test } from "@bigtest/suite";
import { App } from "@bigtest/interactor";
import { Input } from  './interactors';

export default test('Basic Test')
  .step('visit /', () => App.visit('/'))
  .child('Logging in', test => test
    .step('enter username', () => Input('username').type('diku_admin'))
    .assertion('username has value', () => Input('username').has({ value: 'diku_admin' })));
